.container {
    height: 600px;
    background-color: #1C1D25;
}

.contentContainer {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.toTop {
    margin-top: 15px;
    transition: 0.4s;
    opacity: 0;
    transform: translateX(-25px);
    color: white;
}

.iconLink {
    cursor: pointer;
    display: flex;
    align-items: center;
    position: relative;
}

.iconLink:hover .toTop {
    opacity: 1;
    transform: translateX(0);
}

.linkContainer {
    margin-right: 35px;
    margin-top: 30px;
}

.license {

    margin-right: 35px;
    color: gray;
}

.licenceContainer {
    margin-top: 40px;
}